<template>
  <div>
    <div v-if="isLoaderIsActive">
      <loader>
        <h3 slot="message">Loading...</h3>
      </loader>
    </div>
    <div v-else>
      <h1>Dashboard! Welcome {{ currentUserName }}</h1>
      <hr />
      <v-data-table
        :headers="headers"
        :items="fetchedUsersData"
        item-key="name"
        class="elevation-1"
        :search="search"
        :custom-filter="filterText"
      >
        <template v-slot:top>
          <v-text-field
            v-model="search"
            label="Search"
            class="mx-4"
          ></v-text-field>
        </template>
        <template>
          <tr>
            <td></td>
            <td colspan="4"></td>
          </tr>
        </template>
      </v-data-table>
    </div>
  </div>
</template>
<script>
import { eventBus } from "../main";
import Loader from "./Loader";
export default {
  name: "Home",
  components: {
    loader: Loader,
  },
  data: () => ({
    fetchedUsersData: [],
    currentUserID: null,
    usersDataAPI:
      "https://vue-training-3f5e1-default-rtdb.firebaseio.com/data.json",
    currentUserName: "",
    isLoaderIsActive: true,
    search: "",
  }),
  created() {
    eventBus.$emit("isUserSessionActive", true);
    this.axios
      .get(this.usersDataAPI)
      .then((response) => {
        const fetchedUsersData = [];
        for (let key in response.data) {
          fetchedUsersData.push(response.data[key]);
        }
        return fetchedUsersData;
      })
      .then((processedData) => {
        this.fetchedUsersData = processedData;
        this.currentUserID = parseInt(localStorage.getItem("UserID"));
        this.currentUserName = this.fetchedUsersData[this.currentUserID].name;
        this.isLoaderIsActive = false;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  beforeRouteEnter(from, to, next) {
    !localStorage.getItem("UserID") ? next("/home") : next(true);
  },
  computed: {
    headers() {
      return [
        {
          text: "Name",
          align: "start",
          value: "name",
        },
        { text: "Emaill", value: "email" },
        { text: "Language", value: "language" },
      ];
    },
  },
  methods: {
    filterText(value, search) {
      return (
        value !== null &&
        search !== null &&
        typeof value === "string" &&
        value
          .toString()
          .toLowerCase()
          .includes(search.toLowerCase())
      );
    },
  },
};
</script>
<style scoped>
div h1 {
  text-align: center;
  margin-top: 1%;
}
</style>
